<template>
    <div>
        <el-card>
            <h1>设置</h1>
            <el-divider />
            <el-form>
                <el-form-item label="暗色模式：">
                    <el-switch
                        v-model="isDark"
                        style="margin-left: 2px"
                        inline-prompt
                        :active-icon="Moon"
                        :inactive-icon="Sunny"
                    />
                </el-form-item>
                <el-form-item label="清除缓存：">
                    <el-button type="danger" plain @click="clearCache">清除</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script setup>
import { Sunny, Moon } from "@element-plus/icons-vue";
import { useStore } from "vuex";
import { computed, ref, watch } from "vue";
import {ElMessage} from 'element-plus'

const store = useStore();

const isDark = ref(false);
watch(
    () => store.state.isDark,
    val => {
        isDark.value = val;
    },
    {
        immediate: true
    }
);

watch(isDark, val => {
    store.commit("useDark", val);
});

const clearCache = () => {
    ElMessage.info("功能待开发");
};
</script>

